.dollar-icon
	display: inline-block
	position: relative
	font-size: 25px
	color: #4a4a4a
	+opensans-b
	line-height: 1
	&::before,
	&::after
		content: ''
		position: absolute
		width: 4px
		height: 3px
		background-color: #4a4a4a
		left: 50%
		transform: translate(-50%, 0)
	&::before
		top: 1px
	&::after
		bottom: 0
	&.is-disabled
		color: #d3d3d3
		&::before,
		&::after
			background-color: #d3d3d3

